import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components: {
		wPicker
	},
	data() {
		return {
			clubName: '',
			QQgroup: '',
			Venue: '',
			UserName: '',
			phone: '',
			Weixin: '',
			QQ: '',
			regionCode: '',
			regionName: ''
		}
	},
	methods: {
		InputName(v) {
			this.clubName = v.detail
		},
		InputQQgroup(v) {
			this.QQgroup = v.detail
		},
		InputVenue(v) {
			this.Venue = v.detail
		},
		selectRegion() {
			this.$refs.picker1.show()
		},
		onConfirm(v) {
			console.log(v)
			this.regionName = v.result
			this.regionCode = v.checkValue[1]
		},
		InputUserName(v) {
			this.UserName = v.detail
		},
		InputPhone(v) {
			this.phone = v.detail
		},
		InputWeixin(v) {
			this.Weixin = v.detail
		},
		InputQQ(v) {
			this.QQ = v.detail
		},
		submit() {
			const params = {
				clubName: this.clubName,
				qqGroup: this.QQgroup,
				region: this.regionCode,
				venueName: this.Venue,
				name: this.UserName,
				phone: this.phone,
				qq: this.QQ,
				weixin: this.Weixin,
				userId: uni.getStorageSync('userInfo').userId
			}
			console.log(params)
			this.$http.post({
				url: '/v1/rest/club/insertClub',
				data: params
			}).then(resp => {
				console.log(resp)
				if(resp.status == 200) {
					uni.showModal({
						title: '提示',
						content: '申请成功，请等待审核！',
						showCancel: false,
						confirmText: '知道了',
						success: function (res) {
							uni.navigateBack()
						}
					});
				}
			})
		}
	}
}
